import React from 'react';

function DataTable() {
    
    return (
        <div className="table-responsive">
            <table className="table table-sm small">
                <thead className="thead-light">
                    <tr>
                        <th className="text-midnight" scope="col">No.</th>
                        <th className="text-midnight" scope="col">Alarm Level</th>
                        <th className="text-midnight" scope="col">Device Name</th>
                        <th className="text-midnight" scope="col">Alarm Information</th>
                        <th className="text-midnight" scope="col">Trigger Value</th>
                        <th className="text-midnight" scope="col">Alarm Time</th>
                        <th className="text-midnight" scope="col">Acknowledgement</th>
                    </tr>
                </thead>

                <tbody>
                    <tr>
                        <th scope="row">1</th>
                        <td className="text-dark">Critical</td>
                        <td className="text-dark">PDU1</td>
                        <td className="text-dark">Emergency Fan Open Alarm</td>
                        <td className="text-dark">-</td>
                        <td className="text-dark">20-06-2020 23:21:26</td>
                        <td className="text-dark">
                            <a className="btn btn-block btn-sm btn-warning font-weight-bold text-xs" href="#" >
                                Acknowledge</a></td>
                    </tr>
                    <tr>
                        <th scope="row">2</th>
                        <td className="text-dark">Moderate</td>
                        <td className="text-dark">Air Conditioner</td>
                        <td className="text-dark">AC Power-off Alarm</td>
                        <td className="text-dark">OFF</td>
                        <td className="text-dark">20-06-2020 23:21:22</td>
                        <td className="text-dark">
                            <a className="btn btn-block btn-sm btn-warning font-weight-bold text-xs" href="#" >
                                Acknowledge</a></td>
                    </tr>
                    <tr>
                        <th scope="row">3</th>
                        <td className="text-dark">Low</td>
                        <td className="text-dark">Air Conditioner</td>
                        <td className="text-dark">AC Cooling Fault</td>
                        <td className="text-dark">-</td>
                        <td className="text-dark">20-06-2020 23:21:22</td>
                        <td className="text-dark">
                            <a className="btn btn-block btn-sm btn-warning font-weight-bold text-xs" href="#" >
                                Acknowledge</a></td>
                    </tr>
                    <tr>
                        <th scope="row">4</th>
                        <td className="text-dark">Critical</td>
                        <td className="text-dark">TDI</td>
                        <td className="text-dark">Front Door Alarm</td>
                        <td className="text-dark">Open</td>
                        <td className="text-dark">20-06-2020 16:51:60</td>
                        <td className="text-dark">
                            <a className="btn btn-block btn-sm btn-warning font-weight-bold text-xs" href="#" >
                                Acknowledge</a></td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

export default DataTable;